.App {
    text-align: center;
  }
  
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 80px;
  }
  
  .App-header {
    background-image: url("https://vastphotos.com/files/uploads/photos/10177/new-york-skyline-high-resolution-l.jpg");
    height: 250px;
    padding: 40px;
    color: rgb(254, 254, 255);
    background-position: bottom -280px right 0px;
  }
  
  .App-intro {
    font-size: large;
  }
  
  @-webkit-keyframes App-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  
  @keyframes App-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  
  h1{
    display:inline;
    background-color:rgba(8, 32, 99, 0.658);
    color:#fff;
    padding: 15px;
    margin: 5px;
  }
  
  h3{
    display:inline;
    background-color:rgba(8, 32, 99, 0.658);
    color:#fff;
    padding: 15px;
    margin: 5px;
  }

  li{
    color:rgb(10, 0, 0);
    font-weight: bold;
  }


